import OpenAI from "openai";

const openai = new OpenAI({
  apiKey: process.env.REACT_APP_OPENAI_API_KEY,
  dangerouslyAllowBrowser: true,
});

const getGameLink = (game) => {
  switch (game) {
    case "pick3":
      return "https://walottery.com/WinningNumbers/PastDrawings.aspx?gamename=pick3&unittype=year&unitcount=2023";
    case "match4":
      return "https://walottery.com/WinningNumbers/PastDrawings.aspx?gamename=match4&unittype=year&unitcount=2023";
    case "hit5":
      return "https://walottery.com/WinningNumbers/PastDrawings.aspx?gamename=hit5&unittype=year&unitcount=2023";
    case "keno":
      return "https://walottery.com/WinningNumbers/PastDrawings.aspx?gamename=keno&unittype=year&unitcount=2023";
    default:
      return "";
  }
};

const getGamePrompt = (game) => {
  const link = getGameLink(game);
  switch (game) {
    case "pick3":
      return `Select three numbers between 0 and 9 that will most likely be correct base on the previous winning data found ${link}, the numbers can be repeated`;
    case "match4":
      return `Select four numbers between 1 and 24 that will most likely be correct base on the previous winning data found ${link}, the numbers cannot be repeated`;
    case "hit5":
      return `Select five numbers between 1 and 42 that will most likely be correct base on the previous winning data found ${link}, the numbers cannot be repeated`;
    case "keno":
      return `Select numbers between 1 and 80 that will most likely be correct base on the previous winning data found ${link}, the numbers cannot be repeated`;
    default:
      return "";
  }
};

