import React, { useState } from "react";
import { ButtonAi } from "../../components/buttonAi/ButtonAi";
import { randomNumberGenerator } from "../../utils/randomNumberGenerator";
import "./Hit.scss";

function Hit() {
  let tempArray = [];
  let array = [];
  const [answerArray, setAnswerArray] = useState([]);
  const [answer, setAnswer] = useState({
    num0: null,
    num1: null,
    num2: null,
    num3: null,
    num4: null,
  });

  const initialAnswer = answer;

  for (let i = 0; i < 5; i++) {
    let num = `num${i}`;
    array.push(
      <input onChange={handleSubmit} key={i} name={num} type="number" />
    );
  }

  function handleSubmit(event) {
    event.preventDefault();
    const { name, value } = event.target;
    setAnswer((prevValue) => {
      for (let i = 0; i < 20; i++) {
        setAnswer({
          ...prevValue,
          [name]: value,
        });
      }
    });
  }

  // write me a function called getRandomNumber that will take in the answer object and return an array of 5 numbers
  // the numbers should be between 1 and 42
  // the numbers should not be repeated

  const getRandomNumbers = () => {

    const randomNumArray = randomNumberGenerator(5, 42);
    console.log(randomNumArray);
    // let randomArray = [];
    // for (let i = 0; i < 5; i++) {
    //   let randomNum = Math.floor(Math.random() * array.length);
    //   randomArray.push(randomNum);
    //   array.splice(randomNum, 1);
    // }
    setAnswerArray([...answerArray, randomNumArray]);
    // console.log(randomArray);
    // return randomArray;
  };

  function renderReset() {
    setAnswerArray([]);
    setAnswer(initialAnswer);
    document.getElementById("hit-form").reset();
  }

  return (
    <div id="hit">
      <section className="all-sections">
        <h2 id="hit-title">HIT5</h2>
        <form name="form" id="hit-form">
          {array}
        </form>
        <br></br>
        <button type="submit" name="reset" onClick={renderReset}>
          Reset
        </button>
        <button name="reset" onClick={getRandomNumbers}>
        Random
        </button>


        <button type="submit" >
          Generate
        </button>
        <ButtonAi game={"hit5"} />
      </section>
      <div id="hitRows">
        <ol>
          {answerArray.map((items, idx) => (
            <>
              <p style={styles.number}>{idx + 1}</p>
              {items.map((each, index) => (
                <li key={index} className="answer-list" style={styles.list}>
                  {each}
                </li>
              ))}
            </>
          ))}
        </ol>
      </div>
    </div>
  );
}

const styles = {
  list: {
    marginBottom: "15px",
  },
  number: {
    marginBottom: "10px",
    fontSize: "2em",
    width: "90%",
    margin: "auto",
    textDecoration: "underline",
    color: "black",
  },
};

export default Hit;
