export const ButtonAi = (props) => {
  const [result, setResult] = useState([]);

  const { game, getResults } = props;

  const generateNumbers = async () => {
    try {
      console.log('this is game aaaa', game)
      const response = await requestFromApi(game);
      setResult([...result, response]);
      // getResults(result);
    } catch (error) {
      alert(error.message);
    }
    console.log('this is game', game);
  };
 
  return (
    <div>
      <button onClick={generateNumbers} name="reset">
       <img src="../../../public/images/chip.png" /> Ai
      </button>
      <a href="https://www.flaticon.com/free-icons/artificial-intelligence" title="artificial intelligence icons">Artificial intelligence icons created by Triangle Squad - Flaticon</a>
    </div>
  );
};
